import './App.css';
import { Model } from "./Model";
import { Suspense, useState, useRef } from 'react'
import { OrbitControls, MapControls, Html, Stats } from '@react-three/drei';
import { Canvas, useFrame } from '@react-three/fiber';
import {
  BuildingOffice2Icon,
  ChartBarSquareIcon,
  Cog6ToothIcon,
  FolderIcon,
  GlobeAltIcon,
  ServerIcon,
  SignalIcon,
  XMarkIcon,
} from '@heroicons/react/24/outline'

import * as THREE from 'three'
import Header from "./Header"
import {
  Bars3Icon,
  CalendarDaysIcon,
  CreditCardIcon,
  EllipsisVerticalIcon,
  FaceFrownIcon,
  FaceSmileIcon,
  FireIcon,
  HandThumbUpIcon,
  HeartIcon,
  PaperClipIcon,
  UserCircleIcon,
  XMarkIcon as XMarkIconMini,
} from '@heroicons/react/20/solid'
import annotations from './annotations.json';


function Annotations({ selected, gotoAnnotation }) {
  return (
    <>
      {annotations.map((a, i) => {
        return (
          <Html key={i} position={[a.lookAt.x, a.lookAt.y, a.lookAt.z]}>
            <svg height="34" width="34" transform="translate(-16 -16)" style={{ cursor: 'pointer' }}>
              <circle cx="17" cy="17" r="16" stroke="rgb(209, 213, 219)" strokeWidth="2" fill="rgba(17, 24, 39,0.66)" onClick={() => gotoAnnotation(i)} />
              <text x="12" y="22" fill="rgb(209, 213, 219)" fontSize={17} fontFamily="monospace" style={{ pointerEvents: 'none' }}>
                {i + 1}
              </text>
            </svg>
            {a.description && i === selected && (
              <div id={'desc_' + i} className="text-gray-300 hover:bg-gray-700 hover:text-white rounded-md px-3 py-2 text-sm font-medium" dangerouslySetInnerHTML={{ __html: a.description }} />
            )}
          </Html>
        )
      })}
    </>
  )
}

const navigation = [
  { name: 'Building one', href: '#', icon: FolderIcon, current: false },
  { name: 'Building two', href: '#', icon: ServerIcon, current: false },
  { name: 'Building three', href: '#', icon: SignalIcon, current: false }
]

function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}


function Buttons({ gotoAnnotation }) {
  return (
    <div id="annotationsPanel" className="md:flex md:items-center md:justify-between bg-gray-800 p-4 rounded-md">
      <div className="mt-4 flex md:mt-0">
        <nav className="flex flex-1 flex-col" aria-label="Sidebar">
          <ul className="-mx-2 space-y-1">
            {annotations.map((a, i) => (
              <li key={a.title}>
                <a
                  href=""
                  onClick={(e) => {e.preventDefault(); gotoAnnotation(i)}}
                  className={classNames(
                    a.current
                      ? 'bg-gray-800 text-white'
                      : 'text-gray-400 hover:text-white hover:bg-gray-800',
                    'group flex gap-x-3 rounded-md p-2 text-sm leading-6 font-semibold'
                  )}
                >
                  <BuildingOffice2Icon className="h-6 w-6 shrink-0" aria-hidden="true" />
                  {a.title}
                </a>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    </div>
  )
}


function Animate({ controls, lerping, to, target }) {
  useFrame(({ camera, gl }, delta) => {
    gl.toneMapping = THREE.NoToneMapping;
    if (lerping) {
      camera.position.lerp(to, delta * 2)
      controls.current.target.lerp(target, delta * 2)

    }
  })
}

function App() {

  const [selected, setSelected] = useState(-1)
  const ref = useRef()
  const [lerping, setLerping] = useState(false)
  const [to, setTo] = useState()
  const [target, setTarget] = useState()



  function gotoAnnotation(idx) {
    setTo(annotations[idx].camPos)
    setTarget(annotations[idx].lookAt)
    setSelected(idx)
    setLerping(true)
  }
  return (
    <>
      <Canvas dpr={[1, 2]}
        camera={{ position: [245.422, 22.136, 222.799] }}
        onCreated={({ gl }) => {
          gl.toneMapping = THREE.NoToneMapping;
          const canvas = gl.domElement;
          const width = canvas.clientWidth;
          const height = canvas.clientHeight;
          gl.setSize(width, height, false);
        }}>

        <OrbitControls ref={ref} target={[119.03, -26.765, 36.794]} />

        <Suspense fallback={null}>
          <hemisphereLight intensity={2}
            position={[0, 10, 0]}
            color='white'
          /* groundColor='#FFAA00' */
          />
          <MapControls />
          <Model />
          <Annotations selected={selected} gotoAnnotation={gotoAnnotation} />

          <Animate controls={ref} lerping={lerping} to={to} target={target} />
        </Suspense>
      </Canvas>

      <Buttons gotoAnnotation={gotoAnnotation} />
    </>
  );
}

export default App;
